body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Pomodoro {
    position: relative;
    text-align: center;
    background: linear-gradient(rgba(102, 125, 182, 0.7), rgba(0, 130, 200, 0.7), rgba(0, 130, 200, 0.7), rgba(102, 125, 182, 0.9)), url(/pomodoro/static/media/clouds.94d3a5fe.jpg);
    background-size: cover;
    /* https://uigradients.com/#Hydrogen */
    color: white;
    min-height: 100vh;
    font-family: 'Roboto', sans-serif;
}

.Pomodoro-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: #FF8B00;
    font-family: 'Righteous', cursive;
    text-shadow: -1px 1px white;
    letter-spacing: 3px;
}

.main {
    min-height: 100vh;
    position: relative;
    padding-bottom: 40px;
}
.Controls {
    margin: 0;
    display: flex;
    justify-content: center;
}
.Label {
    margin: 0 2rem;
}

.adjuster-display {
    margin: 0;
    font-size: 1.2rem;
}


/* https://www.w3schools.com/howto/howto_css_arrows.asp */

.arrow {
    border: solid white;
    border-width: 0 6px 6px 0;
    display: inline-block;
    padding: 6px;
}

.up {
    transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg);
}

.down {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
}
.Timer {
    margin: 1rem auto
}

.time-left {
    font-family: 'Roboto Mono', monospace;
    font-size: 6rem;
    line-height: 1;
    margin: 0;
}
.start_stop {
    padding: 1.4rem;
    padding-bottom: 0.5rem;
}


/* https://www.w3schools.com/howto/tryit.asp?filename=tryhow_css_shapes_triangle-right */

.triangle-right {
    margin: 0 auto;
    background: transparent;
    color: transparent;
    width: 0;
    height: 0;
    border-top: 17px solid transparent;
    border-left: 35px solid white;
    border-right: 0px solid transparent;
    border-bottom: 17px solid transparent;
}

.pause-button {
    margin: 0 auto;
    width: 35px;
    height: 35px;
    border-color: white;
    border-style: double;
    border-width: 0px 35px 0px 0px;
    -webkit-transform: translateX(-25%);
            transform: translateX(-25%);
}
